import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col, Checkbox } from 'antd';
import { trainingTypes } from 'dump/data';

const columns = [
  {
    title: 'Ký hiệu',
    dataIndex: 'sign',
    key: 'sign',
  },
  {
    title: 'Tên loại RL',
    dataIndex: 'type',
    key: 'type',
  },
  {
    title: 'Điểm tối đa',
    dataIndex: 'maxScores',
    key: 'maxScores',
  },
  {
    title: 'Tính tổng điểm',
    dataIndex: 'sumScores',
    key: 'sumScores',
    render: sumScores => <Checkbox checked={sumScores} />,
  },
  {
    title: 'Điểm học tập',
    dataIndex: 'score',
    key: 'score',
    render: sumScores => <Checkbox checked={sumScores} />,
  },
  {
    title: 'Điểm trừ',
    dataIndex: 'subScores',
    key: 'subScores',
    render: sumScores => <Checkbox checked={sumScores} />,
  },
  {
    title: 'Hiển thị',
    dataIndex: 'view',
    key: 'view',
    render: sumScores => <Checkbox checked={sumScores} />,
  },
  {
    title: 'Operation',
    dataIndex: 'operation',
    render: (text, record) =>
      trainingTypes.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#/training/edit/${record.sign}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class TrainingTypePage extends Component {
  state = {};

  render() {
    return (
      <Card title="Loại rèn luyện">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/training/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={trainingTypes} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingTypePage />
  </Page>
);



